<template>
    <div style="height: 100vh">
        <!--    轮播页面-->
        <v-carousel height="100vh" style="width: 100%;" cycle interval="4000" hide-delimiter-background>
            <v-carousel-item
                    v-for="(pic, i) in colors"
                    :key="i"
                    :src="pic.src"
                    reverse-transition="scale-transition"
                    transition="scroll-x-transition"
            >
                <v-row
                        style="margin-left: 15vh; margin-top: 10vh"
                >
                    <!--            "mdi-message-text"-->
                    <v-alert color="rgba(73,148,236,0.8)" icon=newc>
                        <div class="text-h2">
                            {{ colors[i].saying }}
                        </div>
                    </v-alert>
                </v-row>
            </v-carousel-item>
        </v-carousel>

        <v-card shaped width="400" class="mx-auto mt-5" style="float:right; right: 15vh; bottom: 70vh; opacity: 0.9;">
            <v-card-title style="margin-left: 2vh">
                <h1 class="display-1">登录</h1>
            </v-card-title>
            <v-card-text style="margin-left: 2vh; width: 90%" align="center">
                <v-form
                        ref="form"
                        v-model="valid"
                        lazy-validation
                >
                    <!--                           默认显示-->
                    <v-text-field
                            label="账户"
                            v-model="studentID"
                            prepend-icon="mdi-account"
                            :counter="20"
                            :rules="nameRules"
                            required
                    />
                    <!--                           mdi-eye-off 隐藏密码-->
                    <v-text-field
                            :type="showPassword ? 'text' : 'password'"
                            v-model="password"
                            :rules="passwordRules"
                            label="密码"
                            prepend-icon="mdi-lock"
                            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                            @click:append="showPassword = !showPassword"
                            required
                    />
                    <v-autocomplete
                            label="选择你的用户身份"
                            prepend-icon="mdi-face"
                            :items="identities"
                            v-model="identity"
                            :rules="roleRules"

                    ></v-autocomplete>
                </v-form>
            </v-card-text>
            <!--        这个是啥？这个是分割线 -->
            <v-divider></v-divider>

            <v-card-actions>
                <!--			<v-btn color="success">注册</v-btn>-->
                <!--空间-->
                <!--          test  -->
                <v-btn
                        color="success"
                        :to="register.url"
                >
                    {{ register.label }}
                </v-btn>
                <v-spacer></v-spacer>
<!--                <v-btn color="warning" @click='login'>直接登录</v-btn>-->
                <v-btn
                        :disabled="!valid"
                        color="info"
                        @click="this.postLogin"
                >登录
                </v-btn>
            </v-card-actions>
        </v-card>
    </div>
</template>

<script src="https://unpkg.com/axios/dist/axios.min.js"></script>

<script>

    //    动作绑定
    import axios from "axios";
    import $session from '@/service/sessionService'

    export default {
        name: 'Login',

        data() {
            return ({
                valid: true,
                identities: ['学生', '教师', '管理用户'],
                nameRules: [
                    v => !!v || '请输入账户',
                    v => (v && v.length <= 20) || '账户必须小于20个字符',
                ],
                passwordRules: [
                    v => !!v || '请输入密码',
                    v => (v && v.length <= 20) || '输入的密码必须小于20位',
                   v => (v && v.length >= 3) || '输入的密码必须大于3位',
                ],
                roleRules: [
                    v => !!v || 'Role is required',
                ],

                //需要传递的数据
                studentID: '',
                password: '',
                identity: '',

                //存储登录状态
                isLogin: false,

                //返回的信息请求
                resp: '',

                register: {
                    //  label
                    label: '注册',
                    //  vue link
                    url: '/signup'
                },
                showPassword: false,
                colors: [
                    {
                        src: 'https://images.unsplash.com/photo-1523050854058-8df90110c9f1',
                        saying: '良好的志愿填报方案，集成其中',
                        color: 'rgba(73,148,236,0.8)'
                    },
                    {
                        src: 'https://images.unsplash.com/photo-1577036421869-7c8d388d2123',
                        saying: '可靠的预测设计，助你实现梦想',
                        color: 'rgba(0,166,172,0.8)'
                    },
                    {
                        src: 'https://images.unsplash.com/photo-1517486808906-6ca8b3f04846',
                        saying: '完善的管理方案，老师与学生皆可使用',
                        color: 'rgba(190,215,66,0.8)'
                    },
                    {
                        src: 'https://images.unsplash.com/photo-1571260899304-425eee4c7efc',
                        saying: '详实充足的资讯，各大院校信息收录其中',
                        color: 'rgba(245,143,152,0.8)'
                    },
                    {
                        src: 'https://images.unsplash.com/photo-1541339907198-e08756dedf3f',
                        saying: '个性化管理设计，志愿填报全程指导',
                        color: 'rgba(253,185,51,0.8)'
                    }
                ],

            })
        },
        mounted() {
            localStorage.setItem("avatar", "https://images.unsplash.com/photo-1535713875002-d1d0cf377fde");
        },
        methods: {

            //进行登录，并且将当前用户名以及登录状态存储在localstorage中
            login() {
                $session.setCurrentUser({
                    userId: this.userId
                });

                this.$router.replace('/');
            },
            postLogin: function () {
                this.$refs.form.validate()
                var that = this;
                var url = '';
                var tmp = that.password.toString();
                var array = [];

                if (that.identity == '学生') {
                    url = 'http://127.0.0.1:9090/student/login';
                } else if (that.identity == '教师') {
                    url = 'http://127.0.0.1:9090/teacher/login';
                } else {
                    //超级用户，等待后端接口
                    url = 'http://127.0.0.1:9090/admin/login';
                }
                axios({
                    url: url,
                    method: 'post',
                    params: {
                        id: that.studentID,
                        password: that.password,
                    }
                })
                    .then(function (response) {

                        // console.log(response.data.code);
                        if (response.data.code === "0") {
                            that.resp = response;
                            that.isLogin = true;

                            //存储当前用户的登录状态

                            localStorage.setItem("userName", that.studentID);
                            localStorage.setItem("IsLogin", that.isLogin);
                            localStorage.setItem("Identity", that.identity);

                            localStorage.setItem("token", "bearer " + response.data.data);
                            // console.log(localStorage.getItem("token"));
                            axios.defaults.headers.common['token'] = localStorage.getItem("token");

                          if( that.identity ===  '管理用户'){
                            localStorage.setItem("name","管理员");
                          }else {
                            that.getName();
                          }
                            that.$router.push('/layout/ScoreQuery');
                            // / alert(localStorage.getItem("Identity", that.identity));
                            //触发提示：后期可以删除
                            // alert("欢迎登陆，用户：" + that.resp.data.data['name']);

                            //跳转到指定页面
                        } else {
                            alert("您的用户名或密码输入错误，请检查   。");
                        }
                    })
                    .catch(function (err) {
                      //alert(err);
                        alert("您的用户名或密码输入错误，请检查。");
                    })
            },
          getName: function () {
            var that=this;
              var url="";
            if (that.identity == '学生') {
              url = 'http://127.0.0.1:9090/student/getName';
            } else if (that.identity == '教师') {
              url = 'http://127.0.0.1:9090/teacher/getName';
            }
            axios({
              url: url,
              method: 'post',
              params: {
                id: that.studentID,
              }
            })
                .then(function (response) {
                    localStorage.setItem("name",response.data.data[0]['name']);
                    // response.data.data[0]
                })
                .catch(function (err) {
                  alert("错误。");
                })
          },
            //获取用户数据
            mesget: function () {
                var that = this;
                axios.get("http://127.0.0.1:9090/student/all")
                    .then(function (response) {
                        that.resp = response.data;
                        console.log(that.resp);
                    })
            }
        },

    }

</script>

<style></style>
